    .nav {
        width: 90%;
        height: inherit;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .title {
            width: 20%;
            text-align: center;
            line-height: 71.95px;
            color: var(--text-color);
            font-size: 2.4rem;
            font-weight: 400;
            user-select: none;

            .link {
                text-decoration: none;
                background: -webkit-linear-gradient(45deg, #06619c,#02da97);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
        }
    }
}

