@import "../../../styles/root";

.aboutMe {
    z-index: 1;
    width: 100%;
    height: 620px;
    background-color: rgb(255, 255, 255);
    display: flex;
    justify-content:center;
    align-items: center;
    box-sizing: border-box;

    .info{
        width: 1140px;
        height: 90%;
        display: flex;
        /* background-color: rgb(163, 163, 163); */
        justify-content: space-around;
        .content {
            width: 540px;
            height: 100%;
            padding: 15px;
            display: flex;
            flex-direction: column;
            justify-content: center;   
            .name {
                font-size: 2.4rem;
                // color: #111111;
            }

            .task {
                font-size: 1.6rem;
            }

            .description {
                font-size: 1.5rem;
                line-height: 2.25rem;
            }
        }

        .image {
            background-color: rgba(30, 98, 119, 0.5);
            width: 570px;
            height: 100%;
            padding: 15px;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}

.counterRow {
    width: 1100px;
    height: 162px;
    background-color: $white-color;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 27px -3px;
    position: relative;
    top: calc(-162px / 2);
    z-index: 1;
}